import './node_modules/normalize.css/normalize.css'
import './node_modules/nouislider/distribute/nouislider.css'
import noUiSlider from './node_modules/nouislider/distribute/nouislider'

const boxes = document.querySelectorAll('.box')
const boxSize = document.getElementById('slider-box-size')
const boxPadding = document.getElementById('slider-box-padding')
const boxBorder = document.getElementById('slider-box-border')
const boxMargin = document.getElementById('slider-box-margin')

const pxFormat = {
  to: function (value) {
    return Math.round(value) + 'px'
  },
  from: function (value) {
    return Number(value.replace('px', ''))
  }
}

noUiSlider.create(boxSize, {
  start: 200,
  connect: [true, false],
  tooltips: [true],
  step: 10,
  range: {
    min: 10,
    max: 400
  },
  format: pxFormat
});

[boxPadding, boxBorder, boxMargin].forEach((boxSlider) => {
  noUiSlider.create(boxSlider, {
    start: 0,
    connect: [true, false],
    tooltips: [true],
    step: 5,
    range: {
      min: 0,
      max: 50
    },
    format: pxFormat
  })
})

const setBoxSize = (styles) => {
  const boxes = document.querySelectorAll('.box')
  boxes.forEach(function(box) {

    for (const style in styles) {
      const newValue = styles[style]
      if (box.style[style] !== newValue) {
        if (style === 'box-sizing') {
          updateBoxSizing(box, newValue)
        } else {
          box.style[style] = newValue
        }
      }
    }
  })
  }

const updateBoxSizing = (box, boxSizing) => {
  if (box.style.boxSizing === boxSizing) return

  // update box size
  const oldTransition = box.style.transition
  box.style.transition = 'none'
  setTimeout(() => (box.style.transition = oldTransition), 500)
  box.style.boxSizing = boxSizing

  // clear container and preset button classes
  const boxContainer = box.parentElement;
  ['content-box', 'border-box']
    .forEach(c => boxContainer.classList.remove('is-' + c))
  document.querySelectorAll('#presets button')
    .forEach(el => el.classList.remove('active'))

  switch (boxSizing) {
    case 'content-box':
      boxContainer.classList.remove('is-border-box')
      boxContainer.classList.add('is-content-box')
      document.querySelector('button[value="preset-content"]')
        .classList.add('active')
      break
    case 'border-box':
      boxContainer.classList.remove('is-content-box')
      boxContainer.classList.add('is-border-box')
      document.querySelector('button[value="preset-border"]')
        .classList.add('active')
      break
    default:
      break
  }

  boxContainer.classList.add('is-' + boxSizing)
}

/* Event Listeners */
let lastSizedByPreset = false

boxSize.noUiSlider.on('update', (value) => {
  setBoxSize({ width: value[0], height: value[0] })
  lastSizedByPreset = false
})

boxPadding.noUiSlider.on('update', (value) => {
  setBoxSize({ padding: value[0] })
  lastSizedByPreset = false
})

boxMargin.noUiSlider.on('update', (value) => {
  setBoxSize({ margin: value[0] })
  lastSizedByPreset = false
})

boxBorder.noUiSlider.on('update', (value) => {
  setBoxSize({ 'border-width': value[0] })
  lastSizedByPreset = false
})

document.getElementById('presets').addEventListener('click', (ev) => {
  if (ev.target.tagName !== 'BUTTON') return
  switch (ev.target.value) {
    case 'preset-reset':
      boxPadding.noUiSlider.set(0)
      boxBorder.noUiSlider.set(0)
      boxMargin.noUiSlider.set(0)
      lastSizedByPreset = false
      break
    case 'preset-content':
      if (ev.target.classList.contains('active') || lastSizedByPreset) {
        boxSize.noUiSlider.set(200)
        boxPadding.noUiSlider.set(30)
        boxBorder.noUiSlider.set(10)
        boxMargin.noUiSlider.set(20)
        lastSizedByPreset = true
      }
      boxes.forEach(box => updateBoxSizing(box, 'content-box'))
      break
    case 'preset-border':
      if (ev.target.classList.contains('active') || lastSizedByPreset) {
        boxSize.noUiSlider.set(280)
        boxPadding.noUiSlider.set(30)
        boxBorder.noUiSlider.set(10)
        boxMargin.noUiSlider.set(20)
        lastSizedByPreset = true
      }
      boxes.forEach(box => updateBoxSizing(box, 'border-box'))
      break
    default:
      break
  }
})
